@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .navBarTrue {
    @apply absolute flex flex-col justify-center items-center gap-4 rounded-lg bg-blue-100 mdl:bg-transparent dark:bg-bodyColor shadow-md shadow-blue-500/50 mdl:shadow-none w-full mdl:w-full h-[500px] mdl:h-full -top-10 left-0 mdl:static mdl:flex-row mdl:inline-flex  transition-all duration-300;
  }

  .navBarFalse {
    @apply absolute flex flex-col justify-center items-center gap-4 rounded-lg  hidden mdl:static mdl:flex-row mdl:inline-flex;
  }

  .homeIcon {
    @apply w-16 h-16 bg-black bg-opacity-30 text-gray-200 text-xl inline-flex items-center justify-center rounded-md shadow-shadowOne hover:bg-opacity-50 hover:-translate-y-1 transition-all hover:text-designColor cursor-pointer duration-300;
  }

  .projectCard {
    @apply w-full p-4 h-auto gap-4 rounded-lg dark:shadow-shadowOne shadow-lg shadow-cyan-500/50 flex flex-col justify-center items-center cursor-pointer bg-gradient-to-tl dark:from-bodyColor dark:to-[#181F30] from-blue-300 to-transparent  hover:bg-gradient-to-br dark:hover:from-[#202330] dark:hover:to-bodyColor hover:from-blue-300 hover:to-white transition-all duration-500;
  }
  .resumeLi {
    @apply w-full h-12 mdl:h-20 dark:bg-black bg-designColor dark:bg-opacity-25 bg-opacity-50 text-xl dark:text-gray-300 text-blue-950 flex justify-center cursor-pointer hover:bg-opacity-40 duration-300 items-center border-[1px] rounded-[2px];
  }
  .contactInput {
    @apply w-full h-10 rounded-lg border-b-[1px] border-b-gray-600 dark:bg-[#181F30] bg-slate-800 bg-opacity-20 dark:text-lightText text-blue-800 px-4 active:outline-none focus-visible:outline-designColor outline-none focus-visible:border-b-transparent duration-300;
  }
  .contactTextArea {
    @apply w-full rounded-lg border-b-[1px] border-b-gray-600 dark:bg-[#181F30] bg-slate-800 bg-opacity-20 dark:text-lightText text-blue-800 py-1 px-4 active:outline-none focus-visible:outline-designColor outline-none focus-visible:border-b-transparent duration-300 resize-none;
  }
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background-color: #081b27;
}

::-webkit-scrollbar-thumb {
  background-color: #123050;
  border-radius: 25px;
}
::-webkit-scrollbar-thumb:hover {
  background-color: #237492;
}

.project_logo_filter {
  filter: invert(58%) sepia(0%) saturate(5880%) hue-rotate(33deg)
    brightness(103%) contrast(98%);
}

.project_card_front_rotate:hover {
  transform: rotateY(180deg);
  backface-visibility: hidden;
}
.project_card_back_rotate:hover {
  transform: rotateY(180deg);
  backface-visibility: hidden;
}
